.board {
  display: grid;
  grid-template-areas:
    's0 s1 s2'
    's3 s4 s5'
    's6 s7 s8';
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  width: 100%;
  aspect-ratio: 1 / 1;
  padding: 24px;
  border: 1px solid black;
}

.blank {
  color: hsla(0, 0%, 0%, 0);
}

.clickable {
  cursor: pointer;
}
.clickable:hover {
  background-color: whitesmoke;
}

.square {
  border: 1px solid black;
  width: 100%;
  aspect-ratio: 1 / 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sector:nth-child(1),
.sector:nth-child(2),
.sector:nth-child(3) {
  border-top: none;
}
.sector:nth-child(7),
.sector:nth-child(8),
.sector:nth-child(9) {
  border-bottom: none;
}
.sector:nth-child(3n-2) {
  border-left: none;
}
.sector:nth-child(3n) {
  border-right: none;
}
.topSector {
  border: none;
}
.activeBoard {
  background-color: gainsboro;
}

.s0 {
  grid-area: 's0';
}
.s1 {
  grid-area: 's1';
}
.s2 {
  grid-area: 's2';
}
.s3 {
  grid-area: 's3';
}
.s4 {
  grid-area: 's4';
}
.s5 {
  grid-area: 's5';
}
.s6 {
  grid-area: 's6';
}
.s7 {
  grid-area: 's7';
}
.s8 {
  grid-area: 's8';
}
